@import url("https://fonts.googleapis.com/css?family=Montserrat:400,800");

* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
}

nav {
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 1rem;
}

nav ul li a {
  color: white;
  text-decoration: none;
}

header {
  background-color: #ff5722;
  color: white;
  padding: 2rem;
  text-align: center;
}

header img {
  width: 100%;
  height: 800px;
  margin-top: 1rem;
}

main {
  padding: 2rem;
}

main .spice-images {
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
}

main .spice-images img {
  width: 30%;
  height: 500px;
  border-radius: 8px;
}

footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 1rem;
  position: fixed;
  width: 100%;
  bottom: 0;
}
.card {
  border: 1px solid #ccc;
  padding: 20px;
  width: 90%;
  margin: 0 auto;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
