@import url(https://fonts.googleapis.com/css?family=Montserrat:400,800);
* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
}

nav {
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 1rem;
}

nav ul li a {
  color: white;
  text-decoration: none;
}

header {
  background-color: #ff5722;
  color: white;
  padding: 2rem;
  text-align: center;
}

header img {
  width: 100%;
  height: 800px;
  margin-top: 1rem;
}

main {
  padding: 2rem;
}

main .spice-images {
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
}

main .spice-images img {
  width: 30%;
  height: 500px;
  border-radius: 8px;
}

footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 1rem;
  position: fixed;
  width: 100%;
  bottom: 0;
}
.card {
  border: 1px solid #ccc;
  padding: 20px;
  width: 90%;
  margin: 0 auto;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.products-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.product-card {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin: 6px;
    height: 500px;
    text-align: center;
    width: 20%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.product-card img {
    width: 100%;
    height: 350px;
    border-bottom: 1px solid #ddd;
    margin-bottom: 10px;
}

.product-card h3 {
    font-size: 1.5em;
    color: #333;
}

.product-card p {
    color: #555;
}

.product-card p:last-child {
    font-weight: bold;
    color: #000;
}

.products-heading {
    text-align: center;
    margin: 20px 0;
    font-size: 3em;
    color: #ff5722;
    border-bottom: 2px solid #ddd;
    padding-bottom: 10px;
}

/* Responsive Styles */
@media (max-width: 1024px) {
    .product-card {
        width: 30%; 
        height: auto; 
    }

    .product-card img {
        height: 250px; 
    }
}

@media (max-width: 768px) {
    .product-card {
        width: 45%;
        height: auto; 
    }

    .product-card img {
        height: 200px;
    }
}

@media (max-width: 480px) {
    .product-card {
        width: 90%; 
        height: auto; 
    }

    .product-card img {
        height: 150px; 
    }

    .products-heading {
        font-size: 2em; 
    }

    .product-card h3 {
        font-size: 1.2em;
    }

    .product-card p {
        font-size: 1em; 
    }
}

.product-details-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
    margin-bottom: 80px;
}

.product-card {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 16px;
    margin: 16px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.product-image-card {
    flex: 1 1;
    max-width: 400px;
}

.product-image-card img {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
}

.product-info-card {
    flex: 2 1;
    margin-left: 20px;
}

.product-info-card h2 {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: #007bff;
}

.product-info-card p {
    font-size: 1.3em;
    margin: 10px 0;
    color: #8a6262;
}

.product-info-card p.price {
    font-size: 1.5em;
    color: #28a745;
    font-weight: bold;
    padding: 10px;
}

.product-info-card p:last-child {
    font-weight: bold;
    color: #000;
}

.product-quantity {
    margin-top: 20px;
}

.quantity-label {
    font-size: 1.2em;
    color: #333;
    margin-bottom: 20px;
    display: flex;
}

.quantity-options {
    display: flex;
    flex-wrap: wrap; /* Allow items to wrap */
    grid-gap: 10px;
    gap: 10px;
    justify-content: center; /* Center the options */
}

.quantity-option {
    background-color: #e9ecef;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.quantity-option:hover {
    background-color: #dee2e6;
}

.quantity-option.selected {
    background-color: #007bff;
    color: #fff;
}

/* Responsive Styles */
@media (max-width: 1024px) {
    .product-details-container {
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }

    .product-info-card {
        margin-left: 0;
        margin-top: 20px;
        text-align: center;
    }

    .product-info-card h2 {
        font-size: 2em;
    }

    .product-info-card p {
        font-size: 1.2em;
    }

    .product-info-card p.price {
        font-size: 1.4em;
    }

    .quantity-options {
        justify-content: center;
    }
}

@media (max-width: 768px) {
    .product-info-card h2 {
        font-size: 1.8em;
    }

    .product-info-card p {
        font-size: 1.1em;
    }

    .product-info-card p.price {
        font-size: 1.3em;
    }

    .quantity-label {
        font-size: 1em;
    }

    .quantity-option {
        padding: 8px;
    }
}

@media (max-width: 480px) {
    .product-details-container {
        padding: 10px;
    }

    .product-card {
        margin: 10px;
    }

    .product-info-card h2 {
        font-size: 1.6em;
    }

    .product-info-card p {
        font-size: 1em;
    }

    .product-info-card p.price {
        font-size: 1.2em;
    }

    .quantity-label {
        font-size: 0.9em;
    }

    .quantity-option {
        padding: 6px;
    }

    .quantity-options {
        justify-content: center;
        flex-wrap: wrap; /* Ensure options wrap and stay centered */
        grid-gap: 5px;
        gap: 5px;
    }
}

